"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import axios from "axios";
import {
  Activity,
  Cpu,
  Bug,
  Factory,
  PackageSearch,
  ClipboardList,
} from "lucide-react";

// =============================
// Types
// =============================
interface TerminalCounts {
  live: number;
  store: number;
  demo: number;
  debug: number;
  faulty: number;
}

// Icon Map
const iconMap: any = {
  live: Activity,
  store: Factory,
  demo: ClipboardList,
  debug: Bug,
  faulty: PackageSearch,
  tasks: Cpu,
};

// =============================
// Dashboard Card Component
// =============================
function DashboardCard({
  title,
  value,
  gradient,
  onClick,
  icon: Icon,
}: any) {
  return (
    <div
      onClick={onClick}
      className={`
        cursor-pointer select-none
        rounded-2xl p-8 
        shadow-lg 
        bg-gradient-to-br ${gradient}
        hover:shadow-2xl hover:scale-[1.04]
        transition-all duration-300
        flex flex-col justify-center items-center
      `}
    >
      <Icon className="w-14 h-14 mb-5 opacity-95 text-white" />

      <h2 className="text-2xl font-semibold text-white text-center mb-2 drop-shadow-sm">
        {title}
      </h2>

      <p className="text-6xl font-extrabold text-white tracking-wide drop-shadow-md">
        {value ?? "-"}
      </p>
    </div>
  );
}

// =============================
// Main Component
// =============================
export default function HomePage() {
  const [counts, setCounts] = useState<TerminalCounts | null>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  // Fetch counts from backend
  useEffect(() => {
    axios
      .get("http://localhost:8000/terminals/counts")
      .then((res) => {
        setCounts(res.data);
        setLoading(false);
      })
      .catch((err) => {
        console.error("Error fetching terminal counts:", err);
        setLoading(false);
      });
  }, []);

  // Buttons
  const buttons = [
    {
      name: "Live Terminals",
      value: counts?.live,
      gradient: "from-green-400 to-green-600",
      route: "/live",
      icon: iconMap.live,
    },
    {
      name: "Store Terminals",
      value: counts?.store,
      gradient: "from-blue-400 to-blue-600",
      route: "/store",
      icon: iconMap.store,
    },
    {
      name: "Demo Terminals",
      value: counts?.demo,
      gradient: "from-amber-400 to-amber-600",
      route: "/demo",
      icon: iconMap.demo,
    },
    {
      name: "Debug Terminals",
      value: counts?.debug,
      gradient: "from-purple-500 to-purple-700",
      route: "/debug",
      icon: iconMap.debug,
    },
    {
      name: "Faulty Terminals",
      value: counts?.faulty,
      gradient: "from-red-400 to-red-600",
      route: "/faulty",
      icon: iconMap.faulty,
    },
    {
      name: "Task Update",
      value: counts?.faulty,
      gradient: "from-pink-400 to-pink-600",
      route: "/tasks",
      icon: iconMap.tasks,
    },
  ];

  return (
    <div className="min-h-screen flex flex-col items-center bg-gradient-to-b from-gray-50 to-gray-200">
      {/* Header */}
      <header className="w-full bg-white shadow-md flex flex-col items-center py-4">
        <h1 className="text-2xl font-bold text-gray-700 tracking-wide text-center">
          BGI INVENTORY SYSTEM
        </h1>
        <img
          src="/BGI-logo.jpg"
          alt="BGI Logo"
          className="h-24 object-contain mt-1"
        />
      </header>

      {/* Main Section */}
      <main className="flex-1 flex flex-col items-center w-full p-10">
        {loading ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-10 max-w-6xl mt-10">
            {[1, 2, 3, 4, 5, 6].map((n) => (
              <div
                key={n}
                className="h-48 bg-gray-300 animate-pulse rounded-3xl"
              />
            ))}
          </div>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-10 mt-10 max-w-6xl">
            {buttons.map((btn) => (
              <DashboardCard
                key={btn.name}
                title={btn.name}
                value={btn.value}
                gradient={btn.gradient}
                onClick={() => router.push(btn.route)}
                icon={btn.icon}
              />
            ))}
          </div>
        )}
      </main>

      {/* Footer */}
      <footer className="w-full py-6 bg-white border-t text-center text-gray-500 text-sm">
        © {new Date().getFullYear()} BGI • All Rights Reserved
      </footer>
    </div>
  );
}
